import SignIn from "./SignIn";
import Home from "./Home";
import User from "./User";
import Post from "./Post";
import SignUp from "./SignUp";
import NewPost from "./NewPost";
import FriendSuggestion from "./FriendSuggestion";
import FriendRequest from "./FriendRequest";
import UserUpdate from "./UserUpdate";
import UpdatePassword from "./UpdatePassword";
import Album from "./Album";
import EarlyAcess from "./EarlyAcess";
import Friends from "./Friends";
import Result from "./Result";
import Markdown from "./Markdown";
import Test from "./Test";

export default {
  initialRouteName: "home",
  auth: { component: SignIn, requires: ["home"] },
  screens: [
    {
      name: "home",
      component: Home,
      options: { title: "Trang ch" },
    },
    { name: "users", component: User, options: { title: "Trang c nhn" } },
    { name: "posts", component: Post, options: { title: "Trang bi vit" } },
    { name: "signup", component: SignUp, options: { title: "ng k" } },
    {
      name: "newpost",
      component: NewPost,
      options: { title: "To bi vit mi" },
    },
    {
      name: "friendsuggestion",
      component: FriendSuggestion,
      options: { title: "Bn b gi " },
    },
    {
      name: "friendrequest",
      component: FriendRequest,
      options: { title: "Li mi kt bn" },
    },
    {
      name: "userupdate",
      component: UserUpdate,
      options: { title: "Chnh sa trang c nhn" },
    },
    {
      name: "updatepassword",
      component: UpdatePassword,
      options: { title: "i mt khu" },
    },
    {
      name: "album",
      component: Album,
      options: { title: " lu" },
    },
    {
      name: "earlyaccess",
      component: EarlyAcess,
      options: { title: "Kilogram - Sp ra mt" },
    },
    {
      name: "friends",
      component: Friends,
      options: { title: "Tt c bn b" },
    },
    {
      name: "result",
      component: Result,
      options: { title: "Kt qu tm kim" },
    },
    {
      name: "markdown",
      component: Markdown,
      options: { title: "Markdown" },
    },
    {
      name: "test",
      component: Test,
      options: { title: "Test Component" },
    },
  ],
  linking: {
    prefixes: ["https://odanang.net"],
    config: {
      screens: {
        home: "/",
        users: "users/:id",
        posts: "posts/:id",
        signup: "signup",
        newpost: "newpost",
        friendsuggestion: "friendsuggestion",
        friendrequest: "friendrequest",
        userupdate: "userupdate",
        updatepassword: "updatepassword",
        album: "album",
        earlyaccess: "earlyaccess",
        friends: "friends",
        result: "result",
        markdown: "markdown",
        test: "test",
      },
    },
  },
};

/*
Cc trang:
Trang ng nhp                 /login                Form ng nhp
Trang ng k                   /signup               Form ng k
Trang ch                       /                     Nt thm bi vit, newfeed ca ti khon, gi  bn b fixed bn phi
Trang thm bi vit             /newpost              Form thm bi vit *
Trang sa bi vit              /updatepost           Form sa bi vit *
Trang bi vit n              /[postID]             Hin th duy nht 1 bi vit
Trang c nhn                   /[userID]             Hin th thng tin c nhn, c hin quan h bn b
Trang bn b                    /[userID]/friends     Hin th ton b bn b ca user *
Trang gi  bn b              /friendsuggestion     Hin th danh sch bn b gi 
Trang li mi kt bn           /friendrequest        Hin th danh sch li mi kt bn
Trang chnh sa trang c nhn   /userupdate           Form i thng tin c nhn
Trang i mt khu              /userupdate           Form i mt khu
*/
